.App {
  font-family: sans-serif;
  text-align: center;
  background-color: aqua;
  height: 1000px;
  margin: 30px;
}

input {
  height: 40px;
  margin: 10px;
  border-radius: 10px;
}

button {
  height: 40px;
  background-color: white;
  border-radius: 10px;
}

button:hover {
  cursor: pointer;
  background-color: black;
  color: white;
}

li:hover{
  cursor: pointer;
}
